.section1 {
   top: 180px;
   position: relative;
}

.front {
    z-index: 99999;
}

.title {
    width: 300px;
    display: block;
}

@keyframes subtitle {
    to {
        transform: translateY(0px);
        opacity: 1;
    }
}

.subtitle {
    font-family: 'Roboto';
    font-size: 20px;
    text-align: center;
    color: #8186D5;
    transform: translateY(-50px);
    opacity: 0;
    animation: subtitle 1.4s 1s forwards;
}

.seework {
    background: transparent linear-gradient(112deg, #7195F5 0%, #C397F4 100%) 0% 0% no-repeat padding-box;
    box-shadow: 0px 0px 5px #000000B8;
    border: 1px solid #707070;
    border-radius: 13px;
    height: 42px;
    cursor: pointer;
    display: flex;

    &:hover i {
        transform: translate(33px, 0px);
    }

    &:hover i span {
        width: 57px;
        right: 24px;
    }

    i {
        margin: auto;
        text-align: center;
        align-items: center;
        align-self: center;
        transition: .4s;
        color: #FEB57E;

        span {
            overflow: hidden;
            width: 0.1px;
            transition: .4s;
            white-space: nowrap;
            font-size: 15px;
            right: 16px;
            align-items: center;
            align-self: center;
            position: absolute;
            font-family: 'Roboto';
            font-weight: 300;
            color: #FEB57E;
            user-select: none;
        }
    }
}

#jellyfish {
    top: 300px;
    right: 0;
    width: 944.16667px;
    height: 828.333333px;
    background: transparent url('../jellyfish.png');
    opacity: 0.3;
    background-size: cover;
    position: absolute;
    z-index: 0;
}

#title {
    height: 52px;
    top: -50px;
}

.letter {
    stroke: #fcf164;
}

.letter:nth-child(2) {
    stroke-dasharray: 160px;
    stroke-dashoffset: 160px;
    animation: title 0.75s ease-out 0s 1 forwards;
}


.letter:nth-child(3) {
    stroke-dasharray: 72px;
    stroke-dashoffset: 72px;
    animation: title 0.75s ease-out 0.2s 1 forwards;
}

.letter:nth-child(4) {
    stroke-dasharray: 150px;
    stroke-dashoffset: 150px;
    animation: title 0.75s ease-out 0.4s 1 forwards;
}

.letter:nth-child(5) {
    stroke-dasharray: 62px;
    stroke-dashoffset: 62px;
    animation: title 0.75s ease-out 0.6s 1 forwards;
}


.letter:nth-child(6) {
    stroke-dasharray: 101px;
    stroke-dashoffset: 101px;
    animation: title 0.75s ease-out 0.8s 1 forwards;
}

.letter:nth-child(7) {
    stroke-dasharray: 163px;
    stroke-dashoffset: 163px;
    animation: title 0.75s ease-out 1s 1 forwards;
}

.letter:nth-child(8) {
    stroke-dasharray: 70px;
    stroke-dashoffset: 70px;
    animation: title 0.75s ease-out 1.2s 1 forwards;
}

.letter:nth-child(9) {
    stroke-dasharray: 72px;
    stroke-dashoffset: 72px;
    animation: title 0.75s ease-out 1.4s 1 forwards;
}

.letter:nth-child(10) {
    stroke-dasharray: 72px;
    stroke-dashoffset: 72px;
    animation: title 0.75s ease-out 1.6s 1 forwards;
}

@keyframes title {
    to {
        stroke-dashoffset: 0;
    }
}